import Editor from "./editor";
export default {
  components: {
    Editor: Editor
  },
  data: function data() {
    return {
      modalData: {
        title: "添加备注",
        modalBoolean: false
      },
      editorData: '',
      nowIndex: null
    };
  },
  methods: {
    init: function init(data, index) {
      console.log(index);
      this.editorData = data;
      this.nowIndex = index;
      this.$refs.Editor.init(data);
      this.modalData.modalBoolean = true;
    },
    closeModal: function closeModal() {
      this.modalData.modalBoolean = false;
    },
    submitFn: function submitFn() {
      this.editorData = this.editorData.slice(3);
      this.editorData = this.editorData.substring(0, this.editorData.length - 4);
      this.$emit("callBack", this.editorData, this.nowIndex);
      this.editorData = '';
      this.nowIndex = null;
      this.modalData.modalBoolean = false;
    }
  }
};