import editorAnswer from './editorAnswer';
export default {
  components: {
    editorAnswer: editorAnswer
  },
  data: function data() {
    return {
      modalData: {
        title: "主观题评分",
        modalBoolean: false
      },
      nowType: "",
      FormData: {
        rules: {},
        data: {}
      }
    };
  },
  methods: {
    init: function init(type, data) {
      this.nowType = type;

      if (type === 'show') {
        this.modalData.title = '查看';
        this.FormData.data = data;
      } else if (type === 'add') {
        this.modalData.title = '添加批改';
        this.FormData.data = data;
      }

      this.modalData.modalBoolean = true;
    },
    closeModal: function closeModal() {
      this.modalData.modalBoolean = false;
    },
    submitFn: function submitFn() {
      var _this = this;

      for (var i = 0; i < this.FormData.data.problemsAnswer.length; i++) {
        this.FormData.data.problemsAnswer[i] = this.FormData.data.problemsAnswer[i].replace(/\<.*?\>/g, "");
      }

      this.$nextTick(function () {
        _this.$emit("callBack", _this.FormData.data);

        _this.FormData.data = {};
        _this.modalData.modalBoolean = false;
      });
    },
    markAnswer: function markAnswer(data, index) {
      this.$refs.editorAnswer.init(data, index);
    },
    handleEditor: function handleEditor(obj, index) {
      console.log(obj, index);
      this.FormData.data.problemsAnswer.splice(index, 1, obj);
    }
  }
};