import markSubjective from "./common/markSubjective";
import { questionApi } from "@/api/api";
export default {
  data: function data() {
    return {
      // 查询条件
      FormData: {
        data: {
          kw: "",
          pageSize: 20,
          page: 1
        },
        rules: {}
      },
      table: {
        total: 0,
        loading: false,
        height: "515",
        columns: [{
          title: "序号",
          type: "index",
          width: 80,
          align: "center"
        }, {
          title: "专业",
          align: "center",
          tooltip: true,
          key: "stuClass"
        }, {
          title: "学号",
          tooltip: true,
          align: "center",
          width: '200',
          key: "stuID"
        }, {
          title: "姓名",
          align: "center",
          width: '200',
          tooltip: true,
          key: "stuName"
        }, {
          title: "提交时间",
          align: "center",
          tooltip: true,
          width: '300',
          key: "submitTime"
        }, {
          title: "作业",
          align: "center",
          width: '300',
          tooltip: true,
          key: "problem_Title"
        }, {
          title: "操作",
          slot: "action",
          width: 280,
          align: "center"
        }],
        tableData: []
      }
    };
  },
  mounted: function mounted() {
    this.getList();
  },
  methods: {
    addNewQuestion: function addNewQuestion(row) {
      var _this = this;

      questionApi.getSubjectiveData({
        _id: row._id
      }).then(function (res) {
        _this.Message("success", res.data.msg);

        _this.$refs.markSubjective.init("add", res.data.data[0]);
      }).catch(function (err) {
        _this.Message("error", err.data.msg);
      });
    },
    searchFn: function searchFn() {
      this.FormData.data.page = 1;
      this.getList();
    },
    handleQsCallBack: function handleQsCallBack(obj, type) {
      var _this2 = this;

      this.table.loading = true;
      questionApi.updateSubjectiveList({
        _id: obj._id,
        grade: obj.grade,
        problem_Title: obj.problem_Title,
        problemsAnswer: obj.problemsAnswer,
        remark: obj.remark,
        stuClass: obj.stuClass,
        stuID: obj.stuID,
        stuName: obj.stuName,
        submitTime: obj.submitTime
      }).then(function (res) {
        _this2.Message("success", res.data.msg);

        _this2.getList();

        _this2.table.loading = false;
      }).catch(function (err) {
        _this2.Message("error", err.data.msg);

        _this2.getList();

        _this2.table.loading = false;
      });
    },
    // 重置搜索
    resetFn: function resetFn() {
      this.$refs.formList.resetFields();
    },
    // pageSize改变
    pageSizeChange: function pageSizeChange(value) {
      this.FormData.data.pageSize = value;
      this.getList();
    },
    // page改变
    pageChange: function pageChange(value) {
      this.FormData.data.page = value;
      this.getList();
    },
    // 查询列表数据
    getList: function getList() {
      var _this3 = this;

      this.table.loading = true;
      questionApi.getSubjectiveList(this.FormData.data).then(function (res) {
        _this3.table.tableData = res.data.info.list;
        _this3.table.total = res.data.info.count;
        _this3.table.loading = false;
      }).catch(function (err) {
        _this3.Message("error", err.data.msg);

        _this3.table.loading = false;
      });
    },
    // 封装消息提示
    Message: function Message(type, content, duration, closable) {
      var msDuration, msClosable;

      if (duration === null || duration === undefined || duration === "") {
        msDuration = 1.5;
      } else {
        msDuration = duration;
      }

      if (closable === null || closable === undefined || closable === "") {
        msClosable = false;
      } else {
        msClosable = closable;
      }

      this.$Message.destroy();
      this.$Message[type]({
        content: content,
        duration: msDuration,
        closable: msClosable
      });
    }
  },
  components: {
    markSubjective: markSubjective
  }
};